export default class LogoClass extends React.PureComponent {

  componentDidMount() {
    let tumben = anime({
      target: '#svg-logo g path',
      strokeDashoffset: [anime.setDashoffset, 0],
      easing: 'easeInOutSine',
      duration: 1500,
      delay: function(el, i) { return i * 250 },
      direction: 'alternate',
      loop: true
    })
  }

  render() {
    return (
      <LogoView />
    )
  }
}


